
*{padding:0;margin:0
}
ul{list-style:none
}
a{text-decoration:none;color:#b8944d;transition:.5s
}
a:hover{color:#e3a936
}
html,body{height:100%
}
body{background:#fff3c4;color:#666
}
.box{display:flex
}
.box .items{box-sizing:border-box;padding:50px;flex:100%;flex-shrink:0;font-size:18px
}
.box .items .title{font-size:26px
}
.box .items p{margin:30px 0
}
.box .items img{width:100%
}
.box.items_2 .items{flex:50%
}
.gbtn{display:inline-block;box-sizing:border-box;padding:0 20px;height:50px;line-height:50px;text-align:center;background:#b8944d;color:white;border:1px solid #b8944d
}
.gbtn:hover{color:#b8944d;background:white
}
table{width:100%;table-layout:fixed;margin-top:40px
}
table+.gbtn{margin-top:20px;float:right
}
table tr th{padding:20px 15px;text-align:left;font-weight:500;font-size:16px;color:#ffffff;text-transform:uppercase;background:#b8944d
}
table tr td{padding:15px;text-align:left;vertical-align:middle;font-weight:300;font-size:16px;color:#666;border-bottom:1px solid #b8944d
}
.tbl_header{margin-top:50px;background-color:#b8944d
}
.tbl_content{margin-top:0
}
#app{width:1320px;margin:0 auto
}
#app #header{position:relative;height:60px
}
#app #header #top_logo{float:left
}
#app #header #top_logo a{height:60px;line-height:60px
}
#app #header #top_nav{position:relative;height:60px;float:right
}
#app #header #top_nav ul{float:left
}
#app #header #top_nav ul li{position:relative;float:left;margin-left:30px
}
#app #header #top_nav ul li:first-child{margin-left:0
}
#app #header #top_nav ul li a{display:inline-block;width:100%;height:100%;line-height:60px;height:60px
}
#app #header #top_nav ul li:hover .sub_menu{visibility:visible;top:100%;opacity:1
}
#app #header #top_nav ul li .sub_menu{position:absolute;border:1px solid #b8944d;visibility:hidden;top:calc( 100% - 3px);opacity:0;transition:.5s;z-index:500
}
#app #header #top_nav ul li .sub_menu li{width:100%;margin:0;background:white;box-sizing:border-box;padding:0 10px;width:100%;min-width:150px;transition:.5s
}
#app #header #top_nav ul li .sub_menu li a{line-height:40px;height:40px
}
#app #header #top_nav ul li .sub_menu li:hover{background:#b8944d
}
#app #header #top_nav ul li .sub_menu li:hover a{color:white
}
#app .content{min-height:350px
}
#app #footer{color:#b8944d;font-size:14px
}
#app #footer .footer_top{padding:60px 0 30px
}
#app #footer .container{margin:0 auto;width:100%
}
#app #footer .container .row{display:flex;justify-content:center;flex-wrap:wrap
}
#app #footer .container .row .column{flex:25%;box-sizing:border-box;padding:0 30px;margin-bottom:30px
}
#app #footer .container .row .column.footer_logo{flex:100px
}
#app #footer .container .row .column.footer_logo h3{font-size:34px;margin:0 0 20px 0;padding:2px 0 2px 0px;line-height:1;font-weight:700
}
#app #footer .container .row .column.news{overflow:hidden
}
#app #footer .container .row .column.news form{margin-top:20px
}
#app #footer .container .row .column.news input{float:left
}
#app #footer .container .row .column.news input[type=search]{border:0;padding:6px 8px;width:65%
}
#app #footer .container .row .column.news input[type=submit]{background:#b8944d;border:0;width:35%;padding:6px 0;text-align:center;color:#ffffff;cursor:pointer
}
#app #footer .container .row .column h4{position:relative;font-size:14px;font-weight:700;color:#b8944d;text-transform:uppercase;padding-bottom:12px;margin-bottom:20px
}
#app #footer .container .row .column h4:before,#app #footer .container .row .column h4:after{content:'';position:absolute;left:0;bottom:0;height:3px
}
#app #footer .container .row .column h4:before{right:0;background:#997939
}
#app #footer .container .row .column h4:after{width:90px;background:#b8944d
}
#app #footer .container .row .column ul li{border-bottom:1px solid #b8944d;padding:10px 0
}
#app #footer .container .row .column ul li:nth-child(1){padding-top:0
}
#app #footer .container .row .column p{line-height:26px
}
#app #footer>.container{padding:30px 0;text-align:center
}
.mainbanner{height:550px;background:url(./static/img/main_banner.f4a3fbb.jpg) no-repeat;background-size:cover
}
.inner:nth-child(1){margin-top:50px
}
main.subpage .container{display:flex
}
main.subpage .container #lnb{flex:230px;flex-shrink:0;flex-grow:0;box-sizing:border-box;padding-right:50px
}
main.subpage .container #lnb ul li{border-bottom:1px solid #b8944d
}
main.subpage .container #lnb ul li.on a{color:white;background:#b8944d;padding-left:20px
}
main.subpage .container #lnb ul li a{display:inline-block;width:100%;height:100%;line-height:50px;font-size:18px;box-sizing:border-box;transition:0.5s
}
main.subpage .container #lnb ul li a:hover{padding-left:20px
}
main.subpage .container .sub_title span{position:relative;display:inline-block;padding-bottom:8px;line-height:43px
}
main.subpage .container .sub_title span:after{content:'';position:absolute;height:3px;background:#b8944d;bottom:0;left:0;right:0
}
main.subpage .container .inner+.inner{margin-top:50px;padding-top:50px;border-top:1px solid #b8944d
}
main.subpage .container .inner .box{flex-wrap:wrap
}
main.subpage .container .inner .box .items{padding:50px 0
}
